body{
    background-color: rgba(41, 41, 41, 0.966);
}


.container {
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  width: 600px;
  background-color: rgba(41, 41, 41, 0.61);
}

.post {
  background-color: rgb(41, 41, 41);
  color: aliceblue;
  height: 360px;
  width: 600px;
}

.post-header{
 display: flex;
}

.author-photo {
    max-height: 20px;
  height: auto;
  width: auto;
  max-width: 20px;
  border-radius: 50px;
  margin: 5px 5px 5px 5px;
}

.author-name {
  margin: 5px 5px 5px 5px;
}

.post-header-icon{
    margin-top: 10px;
    height: 10px;
    width: 10px;
}

.post-header-nickname, .post-header-date{
    font-size: 13px;
    margin-top: 9px;
    margin-left: 15px;
color:dimgray;
}

.post-body {
  justify-content: center;
  text-align: center;
}

.content-text {
  position: absolute;
  margin-left: 25px;
}

.content-img {
  max-width: 550px;
  margin-top: 30px;
  border-radius: 10px;
  max-height: 250px;
}


.post-footer-ul{
    list-style-type: none;
    display: flex;
    justify-content: space-around;
}




.post-footer-icon{
    height: 18px;
    width: 18px;
}